//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }


$fredoka: 'Fredoka One';
@mixin ff {
	font-family: $fredoka, 'Open Sans', sans-serif; }

@font-face {
	@include fontdefault(normal, 400, $fredoka);
	@include fontpath('Fredoka One', 'FredokaOne-Regular', FredokaOne-Regular); }

$neucha: 'Neucha';
@mixin fn {
	font-family: $neucha, 'Open Sans', sans-serif; }

@font-face {
	@include fontdefault(normal, 400, $neucha);
	@include fontpath('Neucha', 'Neucha', Neucha); }
