.select2-dropdown { position: relative; opacity: 0; visibility: hidden; transition: opacity 0.3s, visibility 0.3s; display: block !important; }

.select2-dropdown.is-open { opacity: 1; visibility: visible; transition-delay: .1s; }

.select2-results__option[aria-selected], .select2-container--default .select2-results__option[aria-disabled=true] { padding: 4px 30px; font-family: "Neucha", "Open Sans", sans-serif; transition: color .3s; font-size: 22px; }

.select2-container--default .select2-results__option--highlighted[aria-selected] { color: currentColor; background-color: transparent; }

.select2-results__option { border-top: 1px solid #E6F1E8; }

.no-touch .select2-results__option:hover { color: #44A15B; background-color: transparent; }

.select2-container--default .select2-results__option[aria-disabled=true] { color: rgba(0, 0, 0, 0.3); opacity: 1; }

.select2-container--default .select2-results__option[aria-selected=true] { color: #44A15B; background-color: transparent; }

[data-anim] { position: relative; }

[data-anim-text-parent] { overflow: hidden; display: block; }

[data-anim="text-from-bottom"] { display: block; transform: translateY(102%); }

[data-anim="from-top"] { transform: translateY(-60px); opacity: 0; }

[data-anim="from-bottom"] { transform: translateY(100px); opacity: 0; }

[data-anim="from-left"] { transform: translateX(-100px); opacity: 0; }

[data-anim="from-right"] { transform: translateX(100px); opacity: 0; }

[data-anim="fade"] { opacity: 0; }
