body {
	@include fn;
	@include f(30px,400,1.6);
	@include fadeout(.6s);
	background-color: $mainBg;
	background-repeat: no-repeat;
	background-position: center;
	background-image: url(../img/symbols.svg);
	&.is-loaded {
		@include fadein; } }


.text-green {
	color: $green; }

.title-gl {
	@include ff;
	@include f(72px,400,1.25); }

.title-md {
	@include ff;
	@include f(54px,400,1.67); }

.title-sm {
	@include ff;
	@include f(36px,400,1.34); }
